.weatherHolder {
    width: 100vw;
    height: 100vh;
    margin: 0;
    display: flex;
    align-items: center;
    scroll-behavior: smooth;
    position: absolute;
    z-index: 99;
}

.animationHolder {
    width: 100vw;
    height: 100vh;
    margin: 0;
    overflow: hidden;
    position: absolute;
}

.loading {
    width: 100vw;
    height: 100vh;
    background-color: black;
}

@media screen and (max-width: 756px) {
    .weatherHolder {
        width: 100vw;
        height: 100vh;
    }
}