.landscapeDay {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 29;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: bottom;
    background-image: url(../img/landscape.svg);
}

.landscapeNight {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 29;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: bottom;
    background-image: url(../img/landscape.svg);
    -webkit-filter: grayscale(75%);
    filter: grayscale(75%);
}

.skyDay {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 9;
    background-image: linear-gradient(20deg, #92EFFD, #514eff);;
}

.skyNight {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 9;
    background-image: linear-gradient(20deg, #bdc3c7, #2c3e50);;
}

.cloud {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 19;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: bottom;
    background-image: url(../img/cloud.svg);
    opacity: 0.5;
    animation: cloudMoving 2s none ease;
}

.rain {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 39;
    filter: blur(1px);
}

.rainDrop {
    height: 10%;
    opacity: 0;
    width: 2px;
    border-radius: 2px;
    background-color: #ffffff;
    animation: rainDropAnime 5s infinite linear;
    position: absolute;
}

@media screen and (max-width: 756px) {
    .landscapeDay, .landscapeNight, .cloud {
        background-size: contain;
    }
}

@keyframes cloudMoving {
    0% {
        opacity: 0;
        transform: translateY(-3%);
    }
    100% {
        opacity: 0.5;
        transform: translateY(0);
    }
}

@keyframes rainDropAnime {
    0% {
        opacity: 0;
        transform: translateY(0);
    }
    10%{
        opacity: 0.5;
    }

    100% {
        opacity: 0;
        transform: translateY(1000%);
    }
}