.weatherBlock {
    width: 50%;
    min-width: 450px;
    margin-left: auto;
    margin-right: auto;
    transition: all 0.5s ease;
}

.todayWeather {
    width: 100%;
    display: flex;
    justify-content: center;
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 2em 2em 0 0;
    padding: .5em;
    margin-left: -0.5em;
    backdrop-filter: blur(10px)
}

.nameTemp, .weatherCondition , .riseSetTime {
    margin-left: auto;
    margin-right: auto;
}

.currentImage {
    height: 100px;
}

.currentSunrise, .currentSunset {
    width: 100%;
    height: 40px;
    line-height: 40px;
}

.sunriseImg, .sunsetImg {
    height: 40px;
    float: left;
}

.sunriseText, .sunsetText {
    float: left;
}

.futureWeather {
    width: 100%;
    height: auto;
    background-color: rgba(255, 255, 255, 0.3);
    border-radius: 0 0 2em 2em;
    padding: .5em;
    margin-left: -0.5em;
    backdrop-filter: blur(10px);
}

.futureWeatherImg {
    height: 60px;
    margin-top: -15px;
    margin-bottom: -15px;
}

.futureSunrise, .futureSunset {
    display: flex;
    justify-content: center;
    line-height: 30px;
}

.futureSunriseImg, .futureSunsetImg {
    height: 30px;
}

.futureSunriseText, .futureSunsetText {
    
}

.dayOneWeather, .dayTwoWeather, .dayThreeWeather {
    float: left;
    width: 33%;
    text-align: center;
}

.mobileBreakLine {
    display: none;
}

@media screen and (max-width: 756px) {
    .weatherBlock {
        width: 60%;
        margin-left: auto;
        margin-right: auto;
        min-width: 380px;
    }
    .dayOneWeather, .dayTwoWeather, .dayThreeWeather {
        float: left;
        width: 100%;
    }

    .dayOneText, .dayTwoText, .dayThreeText {
        float: left;
        width: 20%;
        margin-top: auto;
        margin-bottom: auto;
    }

    .futureWeatherImg {
        float: left;
        width: 20%;
        margin-top: -15px;
        margin-bottom: -15px;
    }

    .futureSunrise {
        float: left;
    }

    .mobileBreakLine {
        display: inline;
        margin-top: 5px;
        margin-bottom: 5px;
        width: 100%;
        height: 1px;
        background-color: rgba(255, 255, 255, 0.5);
        float: left;
    }
}